
import Sketch from 'react-p5';
function App() {
   const setup = (p5, canvasParentRef) => {
     p5.createCanvas(200,200).parent(canvasParentRef);

   };

   const draw = (p5) => {
     p5.background(220);
     p5.stroke(0,204, 172);
     p5.strokeWeight(4);
     p5.line(50,105, 100,50);
     p5.line(140,105,100,50);
     p5.strokeWeight(6);
     p5.line(70,50,130,50);
     p5.strokeWeight(0);
     p5.fill(255,109, 144);
     p5.ellipse(70,130,50,50);
     p5.ellipse(140,130,50,50);
   };

   const onSubmit = () => {
       alert('submitted');
   };

   const onKeyUp = (event) => {
    if(event.keyCode === 13) { //enter key
      onSubmit();
   }
    // console.log('key up');

   };

  return (
    <div className="App">
      <input onKeyUp = {onKeyUp} />
      <button onClick={onSubmit}>Submit</button>
      <Sketch setup = {setup} draw = {draw} />

    </div>

    

  );
}

export default App;
